.p-contact-heading {
  margin-bottom: 60px;
  text-align: center;

  transform: translate(0 ,40px);
    opacity: 0;
    transition: .4s;
    transition-delay: .4s;

    &.is-active {
      transform: translate(0 ,0);
      opacity: 1;
    }
  
  &__main {
    font-family: $japanFontSefif;
    font-size: $pc-h1;
    font-weight: 300;
    letter-spacing: .1em;
    line-height: 1;
    margin-bottom: 37px;
    user-select: none;
  }

  &__sub {
    letter-spacing: .01em;
    line-height: 1;
    user-select: none;

    @include mq(tb){
      letter-spacing: .1em;
    }
  }
}