.l-collection {
  padding-top: 100px;
  padding-bottom: 80px;
  
  @include mq(pc){
    padding-top: 205px;
    padding-bottom: 110px;
  }

  &__move {
    transform: translate(0 ,40px);
    opacity: 0;
    transition: .4s;
    transition-delay: .4s;

    &.is-active {
      transform: translate(0 ,0);
      opacity: 1;
    }
  }

  &__heading {
    display: flex;
    justify-content: center;
    width: 100%;

    
    @include mq(pc){
      display: inline-block;
      width: auto;
      position: absolute;
      top: 0;
      left: 0;
      margin-left: 30px;
      background-color: #000000;
      opacity: .9;
      padding: 0 4px 4px;
    }

    @include mq(xl){
      margin-left: 0;
    }

  }

  &__bg {
    background: url("../images/common/bg-shape03.svg")no-repeat right 15% bottom 15%;
    background-size: 50%;
    padding-top: 55px;
    padding-bottom: 80px;

    @include mq(pc){
      background: url("../images/common/bg-shape03.svg")no-repeat left 15% bottom 15%;
      background-size: auto;
    }
  }


  &__slider {
    width: 100%;
    margin: 0 auto;
    position: relative;

    transform: translate(0 ,40px);
    opacity: 0;
    transition: .4s;
    transition-delay: .8s;

    &.is-active {
      transform: translate(0 ,0);
      opacity: 1;
    }
  }

  &__more-button {
    display: flex;
    justify-content: center;
    align-items: center;

    transform: translate(0 ,40px);
    opacity: 0;
    transition: .4s;
    transition-delay: 1.2s;

    &.is-active {
      transform: translate(0 ,0);
      opacity: 1;
    }
  }

}