.c-service-item {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #555555;
  padding: 35px 15px 45px;
  margin-bottom: 45px;

  transform: translate(0 ,40px);
    opacity: 0;
    transition: .4s;

    &:nth-of-type(1){
      transition-delay: .8s;
    }
    &:nth-of-type(2){
      transition-delay: 1.2s;
    }
    &:nth-of-type(3){
      transition-delay: 1.6s;
    }
    

    &.is-active {
      transform: translate(0 ,0);
      opacity: 1;
    }
  
  &:last-of-type{
    margin-bottom: 15px;
  }
  
  @include mq(tb){
    width: 31%;
    padding: 65px 30px 75px;
    margin-bottom: 0;

    &:last-of-type{
      margin-bottom: 0px;
    }
    
  }

  @include mq(pc){
    padding: 65px 55px 75px;
  }

  &__icon {
    display: block;
    height: 65px;
    margin-bottom: 40px;
  }


  &__title {
    letter-spacing: .1em;
    line-height: 1;
    margin-bottom: 24px;
    user-select: none;
  }

  &__text {
    letter-spacing: .1em;
    line-height: 1.6;
    user-select: none;
  }
}