
.p-about-img {

  @include mq(pc){
    display: flex;
  }

  &__left {
    width: 100%;
    padding: 15px 30px;

    transform: translate(0 ,40px);
    opacity: 0;
    transition: .4s;
    transition-delay: .4s;

    &.is-active {
      transform: translate(0 ,0);
      opacity: 1;
    }

    @include mq(pc){
      width: 62.5%;
      padding: 0;
      transition-delay: .4s;
    }

    img {
      width: 100%;
      object-fit: cover;
      font-family: 'object-fit: cover;'
    }
  }

  &__right {
    width: 100%;
    padding: 15px 30px;

    transform: translate(0 ,40px);
    opacity: 0;
    transition: .4s;
    transition-delay: .4s;

    &.is-active {
      transform: translate(0 ,0);
      opacity: 1;
    }

    @include mq(pc){
      width: 37.5%;
      padding: 0;
      transition-delay: .8s;
    }

    img {
      width: 100%;
      object-fit: cover;
      font-family: 'object-fit: cover;'
    }
  }
}