.p-contact-foot {
  text-align: center;
  letter-spacing: .1em;
  line-height: 1.6;

  transform: translate(0 ,40px);
    opacity: 0;
    transition: .4s;
    transition-delay: 1.6s;

    &.is-active {
      transform: translate(0 ,0);
      opacity: 1;
    }

  @include mq(tb){
    line-height: 1;
  }


  &__text {
    margin-bottom: 38px;
    user-select: none;
  }

  


}